/*  */
.settings-container {
    flex: 1;
    margin: 0 auto;
    padding: 3rem;
    box-sizing: border-box;
    max-width: 1400px;
    overflow-y: auto;
}

/*  */
.settings-title {
    font-size: 40px;
    margin-bottom: 2rem;
}

/*   */
.settings-tabs {
    display: flex;
    gap: 10px;
    margin-bottom: 1rem;
}

.tab {
    padding: 10px 20px;
    font-size: 16px;
    border: none;
    background-color: #f2f2f2;
    color: #888;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.2s ease;
}

.tab.active {
    background-color: white;
    color: #000;
    font-weight: 600;
}

/*  */
.settings-card {
    background-color: #fff;
    padding: 3rem;
    border-radius: 12px;
    box-shadow: none;
}

/*     */
.section-title {
    font-size: 36px;
    margin-bottom: 2rem;
    margin-top: 1px;
}

.section-desc {
    font-size: 20px;
    margin-bottom: 2rem;
    color: #555;
}

/*   */
.profile-image-section {
    display: flex;
    align-items: center;
    gap: 2rem;
    margin-bottom: 2rem;
}

.profile-image {
    width: 150px;
    height: 150px;
    background-color: #ddd;
    border-radius: 50%;
}

/*  */
.upload-btn,
.delete-btn {
    margin-right: 10px;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    border: none;
    border-radius: 6px;
}

.upload-btn {
    background-color: #007bff;
    color: white;
}

.delete-btn {
    background-color: #ccc;
    color: #333;
}

.upload-note {
    font-size: 15px;
    color: #888;
    margin-top: 4px;
}

/*  */
.form-group {
    margin-bottom: 1.5rem;
    display: flex;
    flex-direction: column;
}

.form-row {
    display: flex;
    gap: 2rem;
    margin-bottom: 0.5rem;
}

.form-group.half {
    flex: 1;
}

.form-group label {
    font-weight: 600;
    margin-bottom: 0.5rem;
}

/* input & textarea */
.form-group input[type="text"] {
    all: unset;
    box-sizing: border-box;
    padding: 12px 16px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 8px;
    height: 48px;
    width: 100%;
}

.form-group textarea {
    padding: 16px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 8px;
    resize: none;
    min-height: 120px;
}

/*   */
.small-text {
    font-size: 13px;
    color: #888;
    margin-top: 0.3rem;
}

/*   */
.tech-stack {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    margin-bottom: 1rem;
}

.tag {
    background-color: #007bff;
    color: white;
    padding: 4px 14px;
    border-radius: 20px;
    font-size: 15px;
    display: inline-flex;
    align-items: center;
    gap: 6px;
}

.remove-x {
    cursor: pointer;
    margin-left: 6px;
    font-weight: bold;
    color: white;
    padding: 0 6px;
    border-radius: 50%;
    line-height: 1;
}

.remove-x:hover {
    background-color: rgba(255, 255, 255, 0.2);
}

/*   */
.tech-input-row {
    display: flex;
    gap: 15px;
}

.tech-input-row input {
    flex-grow: 1;
    padding: 12px 16px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 6px;
}

.add-btn {
    background-color: #a684f1;
    color: white;
    border: none;
    padding: 8px 16px;
    border-radius: 6px;
    font-size: 20px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}

/*   */
.social-links {
    display: flex;
    gap: 16px;
}

/*    */
.dark-mode .mypage {
    background-color: #1e1e1e;
    color: #eee;
}

/*    */
.dark-mode .settings-container {
    color: #eee;
}

/*    */
.dark-mode .tab {
    background-color: #2a2a2a;
    color: #bbb;
}

.dark-mode .tab:hover {
    background-color: #3a3a3a;
}

.dark-mode .tab.active {
    background-color: #b88eff;
    color: #1e1e1e;
    font-weight: bold;
}

/*   */
.dark-mode .settings-card {
    background-color: #2a2a2a;
    box-shadow: 0 0 6px rgba(255, 255, 255, 0.05);
}

/*    */
.dark-mode .section-desc {
    color: #bbb;
}

/*     */
.dark-mode .profile-image {
    background-color: #444;
}

/*  /  */
.dark-mode .upload-btn {
    background-color: #b88eff;
    color: #1e1e1e;
}

.dark-mode .delete-btn {
    background-color: #555;
    color: #eee;
}

.dark-mode .upload-note {
    color: #aaa;
}

/*   */
.dark-mode .form-group input[type="text"],
.dark-mode .tech-input-row input {
    background-color: #2a2a2a;
    border: 1px solid #555;
    color: #eee;
}

.dark-mode .form-group textarea {
    background-color: #2a2a2a;
    border: 1px solid #555;
    color: #eee;
}

/*     */
.dark-mode .small-text {
    color: #aaa;
}

/*     */
.dark-mode .tag {
    background-color: #b88eff;
    color: #1e1e1e;
}

/*   X  */
.dark-mode .remove-x {
    color: #1e1e1e;
}

.dark-mode .remove-x:hover {
    background-color: rgba(0, 0, 0, 0.2);
}

/*    */
.dark-mode .add-btn {
    background-color: #b88eff;
    color: #1e1e1e;
}

/*     */
.dark-mode .social-links input {
    background-color: #2a2a2a;
    border: 1px solid #555;
    color: #eee;
}
